.playerStatusPanel {
    background-color: white;
}

@import url('https://fonts.googleapis.com/css?family=Montserrat');

html, body {
  font-family: 'Montserrat', sans-serif;
}

.container {
  position:absolute;
  top: 0;
  left: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
}

.progressBar {
  margin-top: 2vh;
  height: 2vh;
  width: 20vh;
  border-radius: 2vh;
  border: 1px solid wheat;
}

.icon {
  margin-top: 1vh;
  margin-left: 1vh;
  margin-right: 1vh;
  height: 4vh;
  width: 4vh;
}

.progressBarSection {
  display: flex;
}


.filler {
  background: #ff0022;
  height: 100%;
  border-radius: inherit;
  transition: width .2s ease-in;
}
